import {Component} from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import {HKService} from "../../../service/hk.service";

/**
 * Generated class for the HkNewsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-hk-news',
  templateUrl: 'hk-news.html',
})
export class HkNewsPage {
  private option = 0;
  private news = [];
  private page = 1;
  private hasNext = true;

  constructor(public navCtrl: NavController, public navParams: NavParams, private hkService: HKService) {
    this.getNewsData(() => {

    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HkNewsPage');
  }

  optionChange(o) {
    if (this.option == o) return;
    this.option = o;
    this.news = [];
    this.page = 1;
    this.hasNext = true;
    this.getNewsData(() => {

    });
  }

  getNewsData(callback) {
    let that: any = this;
    that.hkService.getNewsData(that.option == 0 ? 'yctj ' : 'zjxs', '', that.page, 5).then((rs) => {
      callback();
      if (rs.code == 0) {
        if (rs.data.length > 0) {
          let temArr=[];
          if(typeof rs.data == 'object' && rs.data.constructor == 'Array'){
            for(let i in rs.data){
              temArr.push(rs.data[i]);
            }
          }else{
            temArr = rs.data;
          }
          that.news = that.news.concat(temArr);
          that.page += 1;
          that.hasNext = true;
        } else {
          that.hasNext = false;
        }
      }
    });
  }

  doInfinite(infiniteScroll) {
    this.getNewsData(() => {
      infiniteScroll.complete();
    });
  }

}



